header {
  display: -webkit-box;
  display: flex;
  -webkit-box-pack: justify;
  justify-content: space-between;
  background-color: #F0544F;
  color: white;
  height: 60px;
  -webkit-box-align: center;
  align-items: center;
  top: 0px;
  width: 100%; }

.logo {
  margin-left: 30px;
